import React, { useState, createContext } from "react";

export const ApplicationContext = createContext();

export const ApplicationStorage = ({ children }) => {
  const [showMenuInstructions, setShowMenuInstructions] = useState(false);
  const [hideInstructions, setHideInstructions] = useState(true);

  return (
    <ApplicationContext.Provider
      value={{
        hideInstructions,
        setHideInstructions,
        showMenuInstructions,
        setShowMenuInstructions,
      }}
    >
      {children}
    </ApplicationContext.Provider>
  );
};
