import React, { useContext, useEffect } from "react";
import { NavLink } from "react-router-dom";
import { MdArrowBack } from "react-icons/md";
import { useHistory } from "react-router-dom";
import * as S from "./TestDescription.style.js";
import { ApplicationContext } from "../ApplicationContext.js";

const TestDescription = () => {
  const history = useHistory();
  const { setHideInstructions } = useContext(ApplicationContext);

  useEffect(() => {
    setHideInstructions(false);
  }, [setHideInstructions]);

  return (
    <S.Wrapper>
      <header>
        <button className="btn-link" onClick={() => history.goBack()}>
          <MdArrowBack size={22} />
        </button>
      </header>
      <div className="container-global">
        <h1>Assista ao video para entender o teste</h1>
        <iframe
          width="560"
          height="315"
          src="https://www.youtube.com/embed/6A3xlcSWGe8"
          title="YouTube video player"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullscreen
        ></iframe>

        <NavLink className="btn" to="/teste">
          Entendi, começar o teste!
        </NavLink>
      </div>
    </S.Wrapper>
  );
};

export default TestDescription;
