import React, { useContext } from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import { ApplicationContext } from "./ApplicationContext";
import Instructions from "./components/Instructions";
import Home from "./pages/Home";
import Test from "./pages/Test";
import TestDescription from "./pages/TestDescription";

const App = () => {
  const { showMenuInstructions, setShowMenuInstructions, hideInstructions } =
    useContext(ApplicationContext);

  return (
    <BrowserRouter>
      {!hideInstructions && (
        <Instructions
          showMenuInstructions={showMenuInstructions}
          setShowMenuInstructions={setShowMenuInstructions}
        />
      )}

      <Switch>
        <Route exact path={"/"} component={Home} />
        <Route path={"/descricao"} component={TestDescription} />
        <Route path={"/teste"} component={Test} />
        {/* <Route path={'./test'} component={Test} />
        <Route path={'./questions'} component={Questions} /> */}
      </Switch>
    </BrowserRouter>
  );
};

export default App;
