import React, { useContext, useEffect } from "react";
import { NavLink } from "react-router-dom";
import { ApplicationContext } from "../ApplicationContext";
import Logo from "../img/logo-buser-white.svg";
import Mock from "../img/mock-buser.png";
import * as S from "./Home.style.js";

const Home = () => {
  const { setHideInstructions } = useContext(ApplicationContext);

  useEffect(() => {
    setHideInstructions(true);
  }, [setHideInstructions]);

  return (
    <S.Wrapper>
      <div className="container-global">
        <div className="container-left">
          <img src={Logo} alt="Logo Buser" />
          <h1>Olá, seja bem-vindo ao teste de usabilidade Buser</h1>
          <NavLink className="btn" to={"/descricao"}>
            Começar
          </NavLink>
        </div>
        <div className="container-right">
          <img src={Mock} alt="Buser" />
        </div>
      </div>
    </S.Wrapper>
  );
};

export default Home;
